* {
  margin: 0;
  padding: 0;
}

body {
  font-family: '맑은 고딕', Arial, sans-serif;
  font-size: 16px;
  color: #333;
}

.header {
  background: blue;
  text-align: center;
}

.header h1 {
  color: #fff;
  font-size: 30px;
}

main {
  padding: 15px 10px;
}

.list01 {
  margin: 10px 0 10px 30px;
}

main .title2 {
  margin: 14px 0;
  padding: 3px 0;
  background-color: #e42f2f;
  color: #fff;
}

main .title3 {
  margin: 10px 0;
  padding: 3px 0;
  background-color: #d27575;
  color: #fff;
}

main .title4 {
  display: inline-block;
  margin: 10px 0;
  padding: 3px 10px;
  background-color: #eacbcb;
}

.footer {
  text-align: center;
  line-height: 40px;
  background: #333;
}

.footer address {
  color: #fff;
  font-style: normal;
}